import React, { Suspense } from "react";
// Router
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
// Redux
import { Provider } from "react-redux";
import store from "./redux/store";

import WithAdminAuth from "./hocs/withAdminAuth";
import WithAuth from "./hocs/withAuth";
import PageLoader from "./components/Loader/PageLoader";
import { adminRoutes, authRoutes, mainRoutes } from "./routes";

const AdminLayout = React.lazy(() => import("./hocs/Layout/AdminLayout"));
const Layout = React.lazy(() => import("./hocs/Layout/Layout"));

function App() {
    return (
        <Provider store={store}>
            <Suspense fallback={<PageLoader />}>
                {/* For Github pages route 
                NOTE: Remove it if you deploy to another host service */}
                <Router basename={"/itoneshop"}>
                    <Switch>
                        {authRoutes.map((route) => (
                            <Route {...route}>
                                <WithAuth>
                                    <Layout>{route.comp}</Layout>
                                </WithAuth>
                            </Route>
                        ))}
                        {mainRoutes.map((route) => (
                            <Route {...route}>
                                <Layout>{route.comp}</Layout>
                            </Route>
                        ))}
                        {adminRoutes.map((route) => (
                            <Route {...route}>
                                <WithAdminAuth>
                                    <AdminLayout>{route.comp}</AdminLayout>
                                </WithAdminAuth>
                            </Route>
                        ))}
                        <Route
                            path="/admin"
                            component={() => {
                                window.location.href =
                                    "https://itoneshop.pythonanywhere.com/admin";
                                return null;
                            }}
                        />
                    </Switch>
                </Router>
            </Suspense>
        </Provider>
    );
}
export default App;
