export const existingWishItem = ({ prevItems, nextItem }) => {
    return prevItems.find((prevItem) => prevItem.id === nextItem.id);
};

export const handleRemoveWishlistItem = ({ prevItems, nextItem }) => {
    // console.log("Removing item", nextItem);
    return prevItems.filter((item) => item.id !== nextItem.id);
};

export const handleAddToWishlist = ({ prevItems, nextItem }) => {
    // console.log("Checking item");
    const wishItemExists = existingWishItem({ prevItems, nextItem });

    if (wishItemExists) {
        // console.log("Found item", nextItem);
        return handleRemoveWishlistItem({ prevItems, nextItem });
    }
    // console.log("Not found adding", nextItem);
    return [...prevItems, nextItem];
};
