import React from "react";
const Home = React.lazy(() => import("./containers/Home"));
const Admin = React.lazy(() => import("./containers/Admin/Admin"));
const AllData = React.lazy(() => import("./containers/Admin/AllData"));
const About = React.lazy(() => import("./containers/About"));
const Contact = React.lazy(() => import("./containers/Contact"));
const Search = React.lazy(() => import("./containers/Search"));
const Wishlist = React.lazy(() => import("./containers/Wishlist"));
const Login = React.lazy(() => import("./containers/Login"));
const Signup = React.lazy(() => import("./containers/Signup"));
const ResetPassword = React.lazy(() => import("./containers/ResetPassword"));
const ResetPasswordConfirm = React.lazy(() =>
    import("./containers/ResetPasswordConfirm")
);
const Products = React.lazy(() => import("./containers/Products"));
const Account = React.lazy(() => import("./containers/Account"));
const Order = React.lazy(() => import("./containers/Order"));
const Product = React.lazy(() => import("./containers/Product"));
const FilterProducts = React.lazy(() => import("./containers/NewProducts"));

export const mainRoutes = [
    {
        path: "/",
        exact: true,
        comp: <Home />,
    },

    {
        path: "/login",
        exact: true,
        comp: <Login />,
    },
    {
        path: "/signup",
        exact: true,
        comp: <Signup />,
    },
    {
        path: "/reset-password",
        exact: true,
        comp: <ResetPassword />,
    },
    {
        path: "/password/reset/confirm/:uid/:token",
        exact: true,
        comp: <ResetPasswordConfirm />,
    },
    {
        path: "/category/:category_slug",
        exact: true,
        comp: <Products />,
    },
    {
        path: "/filter/:url",
        exact: true,
        comp: <FilterProducts />,
    },
    {
        path: "/product/:slug/:id",
        exact: true,
        comp: <Product />,
    },
    {
        path: "/search",
        exact: true,
        comp: <Search />,
    },
    {
        path: "/wishlist",
        exact: true,
        comp: <Wishlist />,
    },
    {
        path: "/about",
        exact: true,
        comp: <About />,
    },
    {
        path: "/contact",
        exact: true,
        comp: <Contact />,
    },
];

export const authRoutes = [
    {
        path: "/account",
        exact: true,
        comp: <Account />,
    },
    {
        path: "/checkout",
        exact: true,
        comp: <Order />,
    },
];
export const adminRoutes = [
    {
        path: "/react-admin/all/:category",
        exact: true,
        comp: <AllData />,
    },
    {
        path: "/react-admin",
        exact: true,
        comp: <Admin />,
    },
];
