export const existingCartItem = ({ prevItems, size }) => {
    return prevItems.find((prevItem) => prevItem?.size.id === size.id);
};

export const handelRemoveCartItem = ({ size, prevCartItems }) => {
    return prevCartItems.filter((item) => item?.size.id !== size.id);
};

export const handleAddToCart = ({ size, prevItems, nextItem }) => {
    const quantityIncrement = 1;

    const cartItemExists = existingCartItem({ prevItems, size });

    // console.log("ADDING TO CART", nextItem);
    if (cartItemExists) {
        return prevItems.map((cartItem) =>
            size?.amount > cartItem?.quantity && cartItem?.size.id === size?.id
                ? {
                      ...cartItem,
                      quantity: cartItem?.quantity + quantityIncrement,
                  }
                : cartItem
        );
    }
    // console.log("THERE IS NO OF THIS", size);
    return [
        ...prevItems,
        {
            ...nextItem,
            size: size,
            quantity: quantityIncrement,
        },
    ];
};

export const handleReduceCartItem = ({
    size,
    prevCartItems,
    cartItemToReduce,
}) => {
    // console.log("TRYING TO REDUCE", cartItemToReduce);
    const existingCartItem = prevCartItems.find(
        (cartItem) => cartItem?.size.id === size.id
    );

    if (existingCartItem.quantity === 1) {
        return prevCartItems.filter(
            (cartItem) => cartItem?.size.id !== existingCartItem?.size.id
        );
    }

    return prevCartItems.map((cartItem) =>
        cartItem?.size.id === existingCartItem?.size.id
            ? {
                  ...cartItem,
                  quantity: cartItem?.quantity - 1,
              }
            : cartItem
    );
};
