import {
	GET_CATEGORIES_SUCCESS,
	LOAD_MORE_PRODUCTS_SUCCESS,
	LOAD_PRODUCTS_SUCCESS,
	LOAD_SLIDERS_SUCCESS,
	LOAD_PRODUCT_SUCCESS,
	ADD_COUPON,
	REMOVE_COUPON,
	LOAD_LOGO,
} from "../types/types";

const initialState = {
	products: [],
	next: null,
	prev: null,
	count: null,
	sliders: [],
	categories: [],
	categories_count: null,
	product: null,
	coupon: null,
	logo: null,
};

export default function products(state = initialState, action) {
	const { type, payload } = action;

	switch (type) {
		case LOAD_PRODUCTS_SUCCESS:
			return {
				...state,
				products: action.results,
				next: action.next,
				prev: action.prev,
				count: action.count,
			};
		case LOAD_MORE_PRODUCTS_SUCCESS:
			return {
				...state,
				products: [...action.old_products, ...action.results],
				next: action.next,
				prev: action.prev,
				count: action.count,
			};
		case GET_CATEGORIES_SUCCESS:
			return {
				...state,
				categories: action.results,
				categories_count: action.count,
			};
		case LOAD_SLIDERS_SUCCESS:
			return {
				...state,
				sliders: payload,
			};
		case LOAD_PRODUCT_SUCCESS:
			return {
				...state,
				product: payload,
			};

		case ADD_COUPON:
			return {
				...state,
				coupon: payload,
			};

		case REMOVE_COUPON:
			return {
				...state,
				coupon: null,
			};
		case LOAD_LOGO:
			return {
				...state,
				logo: payload,
			};
		default:
			return state;
	}
}
