import {
	ADD_TO_WISHLIST,
	REMOVE_WISHLIST_ITEM,
	CLEAR_WISHLIST,
} from "../types/types";

import {
	handleRemoveWishlistItem,
	handleAddToWishlist,
} from "../utils/wishlist";

const INITIAL_STATE = {
	wishItems: [],
};

export default function wishlist(state = INITIAL_STATE, action) {
	switch (action.type) {
		case ADD_TO_WISHLIST:
			return {
				...state,
				wishItems: handleAddToWishlist({
					prevItems: state.wishItems,
					nextItem: action.payload,
				}),
			};
		case REMOVE_WISHLIST_ITEM:
			return {
				...state,
				wishItems: handleRemoveWishlistItem({
					prevWishItems: state.wishItems,
					wishItemToRemove: action.payload,
				}),
			};

		case CLEAR_WISHLIST:
			return {
				...state,
				wishItems: [],
			};

		default:
			return state;
	}
}
