import {
	ADD_TO_CART,
	REMOVE_CART_ITEM,
	REDUCE_CART_ITEM,
	CLEAR_CART,
} from "../types/types";

import {
	handelRemoveCartItem,
	handleAddToCart,
	handleReduceCartItem,
} from "../utils/cart.js";

const INITIAL_STATE = {
	cartItems: [],
};

export default function cart(state = INITIAL_STATE, action) {
	switch (action.type) {
		case ADD_TO_CART:
			return {
				...state,
				cartItems: handleAddToCart({
					size: action.size,
					prevItems: state.cartItems,
					nextItem: action.payload,
				}),
			};
		case REMOVE_CART_ITEM:
			return {
				...state,
				cartItems: handelRemoveCartItem({
					size: action.size,
					prevCartItems: state.cartItems,
				}),
			};
		case REDUCE_CART_ITEM:
			return {
				...state,
				cartItems: handleReduceCartItem({
					size: action.size,
					prevCartItems: state.cartItems,
					cartItemToReduce: action.payload,
				}),
			};
		case CLEAR_CART:
			return {
				...state,
				cartItems: [],
			};

		default:
			return state;
	}
}
