import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";
import Backend from "i18next-http-backend";
// import en from "../../public/locales/en/translation.json";
// import tk from "./locales/tk/translation.json";
// import ru from "./locales/ru/translation.json";

i18n
	.use(Backend)
	// detect user language
	// learn more: https://github.com/i18next/i18next-browser-languageDetector
	.use(LanguageDetector)
	// pass the i18n instance to react-i18next.
	.use(initReactI18next)
	// init i18next
	// for all options read: https://www.i18next.com/overview/configuration-options
	.init({
		debug: process.env.NODE_ENV === "production" ? false : true,
		fallbackLng: "tk",
		whitelist: ["en", "ru", "tk"],
		react: {
			useSuspense: false, //   <---- this will do the magi
		},
		// ns: [
		// 	"account"
		// 	"translation",
		// 	"header",
		// 	"topheader",
		// 	"account",
		// 	"cart",
		// 	"cities",
		// 	"common",
		// 	"footer",
		// 	"form",
		// 	"home",
		// 	"order",
		// 	"product",
		// 	"wishlist",
		// ],
		backend: {
			loadPath: `${process.env.PUBLIC_URL}/locales/{{lng}}/{{ns}}.json`, //Path to the translation files
			addPath: `${process.env.PUBLIC_URL}/locales/{{lng}}/{{ns}}`,
		},
		detection: {
			order: ["localStorage"],
			lookupLocalStorage: "lng",
			checkWhitelist: true,
		},
		supportedLngs: ["en", "ru", "tk"],
		saveMissing: true,
		saveMissingTo: "all",
		interpolation: {
			escapeValue: false, // not needed for react as it escapes by default
		},
	});

i18n.on("languageChanged", function (lng) {
	localStorage.setItem("lng", lng);
});

export default i18n;
